import React from 'react';

const ChartLegend = ({ planets, aspects }) => {
  // Define aspect types and their colors
  const aspectTypes = [
    { name: 'Conjunction', symbol: '', color: '#666' },
    { name: 'Opposition', symbol: '', color: '#f00' },
    { name: 'Trine', symbol: '', color: '#0a0' },
    { name: 'Square', symbol: '', color: '#f60' },
    { name: 'Sextile', symbol: '', color: '#00f' }
  ];

  return (
    <div className="chart-legend">
      <div className="legend-section">
        <h4>Planets</h4>
        <div className="legend-items">
          {planets && planets.map((planet, index) => (
            <div key={index} className="legend-item">
              <span className="legend-symbol" style={{ color: planet.color }}>
                {planet.symbol}
              </span>
              <span className="legend-name">{planet.name}</span>
            </div>
          ))}
        </div>
      </div>
      
      <div className="legend-section">
        <h4>Aspects</h4>
        <div className="legend-items">
          {aspectTypes.map((aspect, index) => (
            <div key={index} className="legend-item">
              <span className="legend-symbol" style={{ color: aspect.color }}>
                {aspect.symbol}
              </span>
              <span className="legend-name">{aspect.name}</span>
            </div>
          ))}
        </div>
      </div>
      
      <div className="legend-section">
        <h4>Special Points</h4>
        <div className="legend-items">
          <div className="legend-item">
            <span className="legend-symbol" style={{ color: '#f00' }}>ASC</span>
            <span className="legend-name">Ascendant</span>
          </div>
          <div className="legend-item">
            <span className="legend-symbol" style={{ color: '#00f' }}>MC</span>
            <span className="legend-name">Midheaven</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ChartLegend; 