import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';
import { format } from 'date-fns';
import ChartLegend from './ChartLegend';

const NatalChart = ({ data }) => {
  const svgRef = useRef(null);
  const tooltipRef = useRef(null);
  const [selectedElement, setSelectedElement] = useState(null);
  
  // Add aspects from data or use empty array if not available
  const aspects = data?.aspects || [];
  
  useEffect(() => {
    if (!data || !svgRef.current) return;
    
    // Clear any existing chart
    d3.select(svgRef.current).selectAll('*').remove();
    
    // Set up dimensions
    const width = 600;
    const height = 600;
    const margin = 50;
    const radius = Math.min(width, height) / 2 - margin;
    
    // Create SVG
    const svg = d3.select(svgRef.current)
      .attr('width', width)
      .attr('height', height)
      .append('g')
      .attr('transform', `translate(${width / 2}, ${height / 2})`);
    
    // Create tooltip
    const tooltip = d3.select(tooltipRef.current)
      .style('opacity', 0)
      .style('position', 'absolute')
      .style('background-color', 'white')
      .style('border', '1px solid #ddd')
      .style('border-radius', '4px')
      .style('padding', '10px')
      .style('pointer-events', 'none')
      .style('font-size', '12px')
      .style('box-shadow', '0 2px 5px rgba(0, 0, 0, 0.1)');
    
    // Draw zodiac wheel
    drawZodiacWheel(svg, radius, data.zodiacSigns);
    
    // Draw house cusps
    drawHouseCusps(svg, radius, data.houses);
    
    // Draw planets
    drawPlanets(svg, radius, data.planets, tooltip);
    
    // Draw aspects
    drawAspects(svg, radius, data.aspects, data.planets);
    
    // Draw ascendant and midheaven
    drawSpecialPoints(svg, radius, data.ascendant, data.midheaven);
    
    // Add chart title
    svg.append('text')
      .attr('class', 'chart-title')
      .attr('x', 0)
      .attr('y', -radius - 20)
      .attr('text-anchor', 'middle')
      .style('font-size', '16px')
      .style('font-weight', 'bold')
      .text(`Natal Chart for ${data.birthData.name}`);
    
    // Add chart subtitle with date and location
    svg.append('text')
      .attr('class', 'chart-subtitle')
      .attr('x', 0)
      .attr('y', -radius - 5)
      .attr('text-anchor', 'middle')
      .style('font-size', '12px')
      .text(`${format(new Date(data.date), 'MMMM d, yyyy')} at ${data.time} - ${data.birthData.place}`);
    
  }, [data]);
  
  // Function to draw the zodiac wheel
  const drawZodiacWheel = (svg, radius, zodiacSigns) => {
    // Create a pie generator for the zodiac signs (each 30 degrees)
    const pie = d3.pie()
      .value(() => 30) // Each sign is 30 degrees
      .sort(null); // Don't sort, maintain the order
    
    // Create an arc generator for the zodiac segments
    const arc = d3.arc()
      .innerRadius(radius * 0.7) // Inner radius for the zodiac wheel
      .outerRadius(radius); // Outer radius
    
    // Create the zodiac segments
    const zodiacSegments = svg.append('g')
      .attr('class', 'zodiac-wheel')
      .selectAll('path')
      .data(pie(zodiacSigns))
      .enter()
      .append('path')
      .attr('d', arc)
      .attr('fill', (d, i) => {
        // Alternate colors based on element
        const element = d.data.element;
        if (element === 'Fire') return '#FFEBCD'; // Light orange
        if (element === 'Earth') return '#E6F5D0'; // Light green
        if (element === 'Air') return '#E6F0FF'; // Light blue
        if (element === 'Water') return '#E6E6FA'; // Light purple
        return '#f0f0f0'; // Default
      })
      .attr('stroke', '#ccc')
      .attr('stroke-width', 1);
    
    // Add zodiac symbols
    const symbolRadius = radius * 0.85; // Position for the symbols
    
    svg.append('g')
      .attr('class', 'zodiac-symbols')
      .selectAll('text')
      .data(zodiacSigns)
      .enter()
      .append('text')
      .attr('x', (d, i) => symbolRadius * Math.sin(((i * 30) + 15) * (Math.PI / 180)))
      .attr('y', (d, i) => -symbolRadius * Math.cos(((i * 30) + 15) * (Math.PI / 180)))
      .attr('text-anchor', 'middle')
      .attr('dominant-baseline', 'central')
      .style('font-size', '18px')
      .text(d => d.symbol);
  };
  
  // Function to draw house cusps
  const drawHouseCusps = (svg, radius, houses) => {
    // Draw house cusp lines
    svg.append('g')
      .attr('class', 'house-cusps')
      .selectAll('line')
      .data(houses)
      .enter()
      .append('line')
      .attr('x1', 0)
      .attr('y1', 0)
      .attr('x2', (d) => radius * Math.sin(d.longitude * (Math.PI / 180)))
      .attr('y2', (d) => -radius * Math.cos(d.longitude * (Math.PI / 180)))
      .attr('stroke', '#666')
      .attr('stroke-width', 1)
      .attr('stroke-dasharray', '3,3');
    
    // Add house numbers
    const houseNumberRadius = radius * 0.6; // Position for house numbers
    
    svg.append('g')
      .attr('class', 'house-numbers')
      .selectAll('text')
      .data(houses)
      .enter()
      .append('text')
      .attr('x', (d) => houseNumberRadius * Math.sin(d.longitude * (Math.PI / 180)))
      .attr('y', (d) => -houseNumberRadius * Math.cos(d.longitude * (Math.PI / 180)))
      .attr('text-anchor', 'middle')
      .attr('dominant-baseline', 'central')
      .style('font-size', '12px')
      .style('fill', '#666')
      .text(d => d.house);
  };
  
  // Function to draw planets
  const drawPlanets = (svg, radius, planets, tooltip) => {
    // Calculate positions for planets
    const planetRadius = radius * 0.5; // Position planets inside the houses
    
    // Create a group for planets
    const planetGroup = svg.append('g')
      .attr('class', 'planets');
    
    // Add planets
    planetGroup.selectAll('g')
      .data(planets)
      .enter()
      .append('g')
      .attr('class', 'planet')
      .attr('transform', (d) => {
        const x = planetRadius * Math.sin(d.longitude * (Math.PI / 180));
        const y = -planetRadius * Math.cos(d.longitude * (Math.PI / 180));
        return `translate(${x}, ${y})`;
      })
      .each(function(d) {
        // Add a circle behind the symbol for better visibility
        d3.select(this)
          .append('circle')
          .attr('r', 12)
          .attr('fill', 'white')
          .attr('stroke', d.color)
          .attr('stroke-width', 1.5);
        
        // Add the planet symbol
        d3.select(this)
          .append('text')
          .attr('text-anchor', 'middle')
          .attr('dominant-baseline', 'central')
          .style('font-size', '14px')
          .style('fill', d.color)
          .text(d.symbol);
        
        // Add retrograde symbol if applicable
        if (d.isRetrograde) {
          d3.select(this)
            .append('text')
            .attr('text-anchor', 'middle')
            .attr('dominant-baseline', 'central')
            .attr('y', 16) // Position below the planet
            .style('font-size', '10px')
            .style('fill', d.color)
            .text(''); // Retrograde symbol
        }
      })
      .on('mouseover', function(event, d) {
        // Show tooltip on hover
        tooltip.transition()
          .duration(200)
          .style('opacity', 0.9);
        
        tooltip.html(`
          <strong>${d.name}</strong><br/>
          ${d.zodiacSignName} ${d.degree}<br/>
          ${d.isRetrograde ? 'Retrograde' : 'Direct'}<br/>
          House: ${getHouseForDegree(d.longitude, data.houses)}
        `)
          .style('left', (event.pageX + 10) + 'px')
          .style('top', (event.pageY - 28) + 'px');
        
        // Highlight the planet
        d3.select(this)
          .select('circle')
          .transition()
          .duration(200)
          .attr('r', 15)
          .attr('stroke-width', 2);
      })
      .on('mouseout', function() {
        // Hide tooltip
        tooltip.transition()
          .duration(500)
          .style('opacity', 0);
        
        // Remove highlight
        d3.select(this)
          .select('circle')
          .transition()
          .duration(200)
          .attr('r', 12)
          .attr('stroke-width', 1.5);
      })
      .on('click', function(event, d) {
        // Set selected element for detailed info
        setSelectedElement(d);
        event.stopPropagation();
      });
  };
  
  // Function to draw aspects between planets
  const drawAspects = (svg, radius, aspects, planets) => {
    // Create a map of planet IDs to their positions
    const planetPositions = {};
    planets.forEach(planet => {
      const angle = planet.longitude * (Math.PI / 180);
      const planetRadius = radius * 0.5;
      planetPositions[planet.planet] = {
        x: planetRadius * Math.sin(angle),
        y: -planetRadius * Math.cos(angle)
      };
    });
    
    // Create a group for aspects
    const aspectGroup = svg.append('g')
      .attr('class', 'aspects');
    
    // Draw aspect lines
    aspectGroup.selectAll('line')
      .data(aspects)
      .enter()
      .append('line')
      .attr('x1', d => planetPositions[d.planet1].x)
      .attr('y1', d => planetPositions[d.planet1].y)
      .attr('x2', d => planetPositions[d.planet2].x)
      .attr('y2', d => planetPositions[d.planet2].y)
      .attr('stroke', d => {
        // Color based on aspect type
        switch (d.aspect) {
          case 'conjunction': return '#666'; // Gray
          case 'opposition': return '#f00'; // Red
          case 'trine': return '#0a0'; // Green
          case 'square': return '#f60'; // Orange
          case 'sextile': return '#00f'; // Blue
          default: return '#999';
        }
      })
      .attr('stroke-width', 1)
      .attr('stroke-dasharray', d => {
        // Different line styles for different aspects
        switch (d.aspect) {
          case 'conjunction': return ''; // Solid
          case 'opposition': return ''; // Solid
          case 'trine': return ''; // Solid
          case 'square': return ''; // Solid
          case 'sextile': return '3,3'; // Dashed
          default: return '3,3';
        }
      })
      .attr('opacity', 0.6);
  };
  
  // Function to draw ascendant and midheaven
  const drawSpecialPoints = (svg, radius, ascendant, midheaven) => {
    // Draw ascendant line
    svg.append('line')
      .attr('class', 'ascendant-line')
      .attr('x1', 0)
      .attr('y1', 0)
      .attr('x2', radius * Math.sin(ascendant.longitude * (Math.PI / 180)))
      .attr('y2', -radius * Math.cos(ascendant.longitude * (Math.PI / 180)))
      .attr('stroke', '#f00')
      .attr('stroke-width', 2);
    
    // Draw midheaven line
    svg.append('line')
      .attr('class', 'midheaven-line')
      .attr('x1', 0)
      .attr('y1', 0)
      .attr('x2', radius * Math.sin(midheaven.longitude * (Math.PI / 180)))
      .attr('y2', -radius * Math.cos(midheaven.longitude * (Math.PI / 180)))
      .attr('stroke', '#00f')
      .attr('stroke-width', 2);
    
    // Add labels for ascendant and midheaven
    const labelRadius = radius + 15;
    
    // Ascendant label
    svg.append('text')
      .attr('class', 'ascendant-label')
      .attr('x', labelRadius * Math.sin(ascendant.longitude * (Math.PI / 180)))
      .attr('y', -labelRadius * Math.cos(ascendant.longitude * (Math.PI / 180)))
      .attr('text-anchor', 'middle')
      .style('font-size', '12px')
      .style('font-weight', 'bold')
      .style('fill', '#f00')
      .text('ASC');
    
    // Midheaven label
    svg.append('text')
      .attr('class', 'midheaven-label')
      .attr('x', labelRadius * Math.sin(midheaven.longitude * (Math.PI / 180)))
      .attr('y', -labelRadius * Math.cos(midheaven.longitude * (Math.PI / 180)))
      .attr('text-anchor', 'middle')
      .style('font-size', '12px')
      .style('font-weight', 'bold')
      .style('fill', '#00f')
      .text('MC');
  };
  
  // Helper function to determine which house a degree falls into
  const getHouseForDegree = (degree, houses) => {
    for (let i = 0; i < houses.length; i++) {
      const currentHouse = houses[i];
      const nextHouse = houses[(i + 1) % houses.length];
      
      let start = currentHouse.longitude;
      let end = nextHouse.longitude;
      
      // Handle case where the house crosses 0
      if (end < start) end += 360;
      
      // Normalize the degree
      let normalizedDegree = degree;
      if (start > end) {
        if (normalizedDegree < start) normalizedDegree += 360;
      }
      
      if (normalizedDegree >= start && normalizedDegree < end) {
        return currentHouse.house;
      }
    }
    
    return '?'; // Unknown house
  };
  
  // Handle click outside of a planet to clear selection
  const handleChartClick = () => {
    setSelectedElement(null);
  };
  
  return (
    <div className="natal-chart-container">
      <div className="chart-svg-container" onClick={handleChartClick}>
        <svg ref={svgRef}></svg>
        <div ref={tooltipRef} className="tooltip"></div>
      </div>
      
      {selectedElement && (
        <div className="chart-details">
          <h3>{selectedElement.name} in {selectedElement.zodiacSignName}</h3>
          <p>
            Position: {selectedElement.degree} {selectedElement.zodiacSignName} {selectedElement.isRetrograde ? '(Retrograde)' : ''}
          </p>
          <p>
            House: {getHouseForDegree(selectedElement.longitude, data.houses)}
          </p>
          <p>
            Element: {selectedElement.element}
          </p>
          <h4>Aspects:</h4>
          <ul>
            {data.aspects
              .filter(aspect => aspect.planet1 === selectedElement.planet || aspect.planet2 === selectedElement.planet)
              .map((aspect, index) => {
                const otherPlanet = aspect.planet1 === selectedElement.planet ? aspect.planet2 : aspect.planet1;
                const otherPlanetData = data.planets.find(p => p.planet === otherPlanet);
                
                return (
                  <li key={index}>
                    {aspect.aspectData.name} to {otherPlanetData.name} (orb: {aspect.orb})
                  </li>
                );
              })}
          </ul>
        </div>
      )}
      
      <ChartLegend planets={data.planets} aspects={aspects} />
    </div>
  );
};

export default NatalChart; 